.hidden{
    display: none;
  }

  .news__wrapper{
    pointer-events: auto;
    margin-bottom: 20px;
    position: relative;
  }

  .news__block-icon{
    fill: #fff;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4,0,.2,1);
    position: absolute;
    right: 12px;
  }

  .news__info{
display: flex;
justify-content: space-between;
  }

  .new__text-wrapper{
    flex-direction: column;
    justify-content: space-between;
    display: flex;
  }



  //BTN test

  .item-news__add-to-favorite {
    position: absolute;
   
    pointer-events: auto;
    z-index: 2;
  
    min-width: 40px;
    min-height: 10px;
    margin: 0;
    padding: 6px 32px 6px 12px;
    right: 12px;
    bottom: 12px;
  
    background: #ffffff00;
    border-radius: 20px;
  
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    text-align: left;
    
  
    color: #111321;
  
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  
    &::after {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      top: 0;
      border-radius: 20px;
      height: 28px;
      width: 126px;
      z-index: -1;
  
      background: #ffffff;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
  
    & .item-news__remove-to-favorite-btn {
      
      position: absolute;
     
      left: -26px;
      width: 153px;
      opacity: 0;
      z-index: 2;
      pointer-events: none;
  
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
  
    & .item-news__remove-to-favorite-btn::after {
      content: '';
      display: block;
      position: absolute;
  
      transform: translateX(0px);
  
      right: 0;
      top: -6px;
      border-radius: 20px;
      height: 28px;
      width: 126px;
      z-index: -1;
  
      background: #ffffff;
  
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
  
    &.hidden-span .item-news__remove-to-favorite-btn {
      
      opacity: 1;
      pointer-events: auto;
  
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  
      .active-news-icon {
        fill: var(--accent-color);
        stroke: var(--accent-color);
  
        transition-duration: 250ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      }
    }
  
    &.hidden-span .item-news__add-to-favorite-btn {
      
      opacity: 0;
      pointer-events: none;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
  
  .item-news__add-to-favorite.hidden-span
    .item-news__remove-to-favorite-btn::after {
    transform: translateX(-42px);
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .item-news__block-icon {
    fill: white;
    position: absolute;
    right: 12px;
  
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .opacity {
    opacity: 0.5;
  }